import React from 'react';
import DropDown from '@src/components/common/DropDown';
import { FaSearch } from 'react-icons/fa';
import { IoSettingsSharp } from 'react-icons/io5';
import { toast } from 'react-toastify';
import { SearchInput, IconWrapper, InputContainer, SearchUsersContainer, ButtonContainer } from './style';

interface Props {
	handleInput: (e: any) => void;
	openModal: (mode: string) => void;
	isAdmin: boolean;
}

const SearchUsers: React.FC<Props> = ({ handleInput, openModal, isAdmin }) => {
	const onSelect = (value: number) => {
		switch (value) {
			case 0:
				openModal('EXIT');
				break;
			case 1:
				openModal('DELETE');
				break;
			case 2:
				openModal('UPDATE');
				break;
			default:
				toast('  ');
		}
	};
	return (
		<SearchUsersContainer>
			<h2></h2>
			<InputContainer>
				<SearchInput type='text' placeholder=' ' onChange={handleInput} />
				<IconWrapper>
					<FaSearch />
				</IconWrapper>
				<ButtonContainer>
					<DropDown
						options={isAdmin ? [' ', ' ', ' '] : [' ']}
						selectedOption={<IoSettingsSharp size='20' />}
						setSelectedOption={onSelect}
					/>
				</ButtonContainer>
			</InputContainer>
		</SearchUsersContainer>
	);
};

export default SearchUsers;
