import React from 'react';
import { DayNameWrapper, WeekNameContainer } from './style';

const MonthHeader: React.FC = () => {
	return (
		<WeekNameContainer>
			{['', '', '', '', '', '', ''].map((day, idx) => (
				<DayNameWrapper key={day} className={idx === 0 ? 'sunday' : ''}>
					{day}
				</DayNameWrapper>
			))}
		</WeekNameContainer>
	);
};

export default MonthHeader;
