import React, { useState } from 'react';
import { FaPencilAlt } from 'react-icons/fa';
import { toast } from 'react-toastify';
import { useRecoilState } from 'recoil';
import userState from '@stores/user';

import { check } from '@apis/auth';
import { updateName } from '@apis/user';
import { nameRegExp } from '@utils/regexs';

import Modal from '@components/common/Modal';
import { Input } from '@components/common/Modal/style';

import { Container, InputContainer } from './style';

export interface Props {
	handleModalClose: () => void;
}

const UpdateModal: React.FC<Props> = ({ handleModalClose }) => {
	const [name, setName] = useState('');
	const [user, setUser] = useRecoilState(userState);
	const handleInput = (e: React.ChangeEvent<HTMLInputElement>) => {
		setName(e.target.value);
	};
	const handleSubmit = () => {
		if (name === '') {
			toast.warn('   !');
		} else if (name === user.name) {
			toast.warn('     !');
		} else if (!nameRegExp.test(name) || name.length > 20) {
			toast.warn('   !');
		} else {
			updateName({ newName: name }, () => {
				check(() => {
					setUser({ ...user, name });
					handleModalClose();
				});
			});
		}
	};
	return (
		<Modal
			title='  '
			handleModalClose={handleModalClose}
			handleSubmit={handleSubmit}
			removeSubmitButton={false}
		>
			<Container>
				<InputContainer>
					<FaPencilAlt />
					<Input placeholder='2-20  , ' value={name} onChange={handleInput} />
				</InputContainer>
			</Container>
		</Modal>
	);
};

export default UpdateModal;
